IListExportJobs, {});\n}, { loading: true });\n\nfunction listJobSteps(jobID: string) {\n    executor.execute(async () => {\n        await executor.executeUseCase<IListExportJobSteps>(IListExportJobSteps, { jobID });\n    }, { loading: true });\n\n    curStepJob.value = viewModel.exportJobs!.find(i => i.jobID == jobID)!;\n}\n\nfunction closeSteps() {\n    curStepJob.value = undefined;\n}\n\nfunction listJobArtifacts(jobID: string) {\n    executor.execute(async () => {\n        await executor.executeUseCase<IListExportJobArtifacts>(IListExportJobArtifacts, { jobID });\n    }, { loading: true });\n\n    curArtifactJob.value = viewModel.exportJobs!.find(i => i.jobID == jobID)!;\n}\n\nfunction chooseStep(stepID: string) {\n    executor.execute(async () => {\n        await executor.executeUseCase<IListExportJobStepLogs>(IListExportJobStepLogs, { stepID });\n    }, { loading: true });\n}\n\nfunction downloadArtifact(artifactID: string, artifactName: string) {\n    executor.execute(async () => {\n        await executor.executeUseCase<IGetExportJobArtifact>(IGetExportJobArtifact, { artifactID, artifactName });\n    }, { loading: true });\n}\n\nfunction closeArtifacts() {\n    curArtifactJob.value = undefined;\n}\n\n</script>\n\n<template>\n    <!-- JOB STEPS AREA -->\n    <div class=\"div-box\" v-if=\"!!curStepJob\">\n        <h3>Export Job Steps for: {{ curStepJob.jobID }}</h3>\n        <div>\n            <p></p>\n        </div>\n        <div v-for=\"step in curStepJob.steps\" :key=\"step.ID\" v-bind:value=\"step.ID\">\n            <button @click=\"chooseStep(step.ID)\">{{ step.name }}</button>\n            <p v-for=\"log in step.logs\">{{ log }}</p>\n        </div>\n        <div>\n            <p></p>\n        </div>\n        <button @click=\"closeSteps()\">Close</button>\n        <div>\n            <p></p>\n        </div>\n    </div>\n\n    <!-- ARTIFACT LIST AREA -->\n    <div class=\"div-box\" v-if=\"!!curArtifactJob\">\n        <h3>Export Job Artifactss for: {{ curArtifactJob.jobID }}</h3>\n        <div>\n            <p></p>\n        </div>\n        <div\n            v-for=\"artifact in curArtifactJob.artifacts\"\n            :key=\"artifact.ID\"\n            v-bind:value=\"artifact.ID\"\n        >\n            {{ artifact.name }}\n            <button @click=\"downloadArtifact(artifact.ID, artifact.name)\">Download</button>\n        </div>\n        <div>\n            <p></p>\n        </div>\n        <button @click=\"closeArtifacts()\">Close</button>\n        <div>\n            <p></p>\n        </div>\n    </div>\n\n    <!-- JOB LIST AREA -->\n    <div>\n        <h3>Export Jobs</h3>\n        <div>\n            <table>\n                <tr>\n                    <th>Job ID</th>\n                    <th>Application</th>\n                    <th>Type</th>\n                    <th>Create Date</th>\n                    <th>Created By</th>\n                    <th>Status</th>\n                    <th></th>\n                </tr>\n                <tr v-for=\"job in viewModel.exportJobs\" :key=\"job.jobID\" v-bind:value=\"job.jobID\">\n                    <td>{{ job.jobID }}</td>\n                    <td>{{ job.jobData.app.name }}</td>\n                    <td>{{ job.jobData.type }}</td>\n                    <td>{{ job.createDate }}</td>\n                    <td>{{ job.createdBy }}</td>\n                    <td>{{ job.status }}</td>\n                    <td>\n                        <button\n                            v-if=\"job.status != 'waiting'\"\n                            @click=\"listJobSteps(job.jobID)\"\n                        >Details</button>\n                        <button\n                            v-if=\"job.status == 'success'\"\n                            @click=\"listJobArtifacts(job.jobID)\"\n                        >Artifacts</button>\n                    </td>\n                </tr>\n            </table>\n        </div>\n    </div>\n</template>\n\n<style scoped>\ntable {\n    font-family: arial, sans-serif;\n    border-collapse: collapse;\n    width: 100%;\n}\n\ntd,\nth {\n    border: 1px solid #dddddd;\n    text-align: left;\n    padding: 8px;\n}\n\ntr:nth-child(even) {\n    background-color: #dddddd;\n}\n\n.div-box {\n    margin-left: 10px;\n    margin-top: 5px;\n    width: 50%;\n    border-style: dotted;\n}\n</style>"],"sourceRoot":""}]);
